import { HashRouter as Router, Routes, Route } from 'react-router-dom';

import Map from './Map';
import CardGame from './CardGame';

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/game" element={<CardGame />} />
        <Route path="/" element={<Map />} />
      </Routes>
    </Router>
  );
}

export default App;
